@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(//cdn.jsdelivr.net/gh/velopert/font-d2coding@1.2.1/d2coding.css);
.page-template main{background:white;min-height:calc(100vh - 15rem)}

.header{background:white;border-bottom:1px solid #ced4da}.header .header-content{height:5rem;width:80rem;margin:0 auto;padding-left:0rem;padding-right:3rem;display:flex;align-items:center}@media (max-width: 1399px){.header .header-content{width:100%}}@media (max-width: 767px){.header .header-content{padding-left:1rem;padding-right:1rem}}.header .header-content .brand{color:black;font-size:1.5rem;font-weight:600}.header .header-content .right{margin-left:auto}.header .header-content .left{margin-right:auto}.header .category{height:5rem;width:1400px;margin:2rem auto;padding-left:3rem;padding-right:3rem;display:flex;align-items:center;justify-content:center}@media (max-width: 1399px){.header .category{width:100%}}@media (max-width: 767px){.header .category{padding-left:1rem;padding-right:1rem}}.header .category .item{color:black;font-size:1rem;font-weight:200;margin:1rem}

.button{padding-left:0.5rem;padding-right:0.5rem;padding-top:0.25rem;padding-botton:0.25rem;font-weight:600;font-size:0.9rem;color:black;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:inline;text-align:center}.button.default{background:#fcc419}.button.default:hover{background:#ffd43b}.button.default:active{background:#fab005}.button.gray{background:#ced4da;width:4rem;height:1.5rem;color:#495057}.button.gray:hover{background:#868e96}.button.gray:active{background:#495057}.button.outline{border:none;border-radius:2px}.button.outline:hover{background:black;color:white}.button.outline:active{background:black;border:2px solid rgba(255,255,255,0.85)}.button:hover{box-shadow:0 3px 6px rgba(0,0,0,0.08),0 3px 6px rgba(0,0,0,0.115)}.button.disabled{background:#ced4da;color:#868e96;cursor:default}.button.disabled:hover,.button.disabled:active{box-shadow:none;background:#ced4da}.button+.button{margin-left:0.5rem}

.footer{background:#ffe066;height:10rem;display:flex;align-items:center;justify-content:center;flex-direction:column}.footer .brand{color:white;font-size:2rem;font-weight:600}.footer .font{margin-top:0.5rem;font-weight:600;font-size:0.8rem;color:rgba(255,255,255,0.8);cursor:pointer}.footer .font:hover{color:white}

.title{padding-top:1rem;padding-bottom:1rem;font-size:2rem;font-weight:400}.list-wrapper{width:70%;margin:0 auto;padding-top:3rem;padding-bottom:3rem;padding-left:1rem;padding-right:1rem}@media (max-width: 1399px){.list-wrapper{width:768px}}@media (max-width: 1023px){.list-wrapper{width:512px}}@media (max-width: 767px){.list-wrapper{width:100%}}

.pagination{margin-top:2rem;display:flex;align-itmes:center}.pagination .number{font-size:0.79rem;text-align:center;color:#868e96;flex:1 1}

.post-list .post-item{padding:1.5rem;-webkit-transition:all .15s ease-in;transition:all .15s ease-in}.post-list .post-item h2{font-size:2rem;font-weight:400;margin:0;color:#495057}.post-list .post-item h2 a{-webkit-transition:all .15s ease-in;transition:all .15s ease-in;border-bottom:1px solid transparent}.post-list .post-item h2 a:hover{color:#228be6;border-bottom:1px solid #228be6}.post-list .post-item .date{font-size:0.79rem;color:#adb5bd}.post-list .post-item p{font-weight:300;color:#495057}.post-list .post-item .tags{font-size:0.79rem;color:#228be6}.post-list .post-item .tags a:hover{color:#339af0;text-decoration:underline}.post-list .post-item .tags a+a{margin-left:0.25rem}.post-list .post-item:hover{background:rgba(34,139,230,0.05)}.post-list .post-item+.post-item{border-top:1px solid #dee2e6}

.search-bar{display:flex;align-items:center;margin-bottom:1rem;font-size:1rem;height:3rem}.search-bar .target{margin:.5em;font-size:1rem;padding:.5em;border:1px solid #ccc;border-color:#dbdbdb #d2d2d2 #d0d0d0 #d2d2d3;box-shadow:inset 0.1em 0.1em 0.15em rgba(0,0,0,0.1);vertical-align:middle;line-height:1.25em;outline:0;width:10%;height:80%}.search-bar input{margin:.5em;margin-right:1rem;font-size:1rem;padding:.5em;border:1px solid #ccc;border-color:#dbdbdb #d2d2d2 #d0d0d0 #d2d2d3;box-shadow:inset 0.1em 0.1em 0.15em rgba(0,0,0,0.1);vertical-align:middle;line-height:1.25em;outline:0;flex:1 1;height:80%}.search-bar .button{width:10%;height:70%}

body{margin:0;box-sizing:border-box;font-family:"Noto Sans KR", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:inherit}a{text-decoration:inherit;color:inherit}

