@import "../../../styles/utils";

.post-list{
  .post-item {
    padding: 1.5rem;
    transition: all .15s ease-in;
    h2{
      font-size: 2rem;
      font-weight: 400;
      margin: 0;
      color: $oc-gray-7;
      a{
        transition: all .15s ease-in;
        border-bottom: 1px solid transparent;
      }
      a:hover {
        color: $oc-blue-6;
        border-bottom: 1px solid $oc-blue-6;
      }
    }
    .date {
      font-size: 0.79rem;
      color: $oc-gray-5;
    }
    p{
      font-weight: 300;
      color: $oc-gray-7;
    }
    .tags{
      font-size: 0.79rem;
      color: $oc-blue-6;
      a{
        &:hover{
          color: $oc-blue-5;
          text-decoration: underline;
        }
      }
      a + a{
        margin-left: 0.25rem;
      }
    }
    &:hover {
      background: rgba($oc-blue-6, 0.05);
    }
  }
  .post-item + .post-item {
    border-top: 1px solid $oc-gray-3;
  }
}