@import "../../../styles/utils";

.search-bar{
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 1rem;
  height: 3rem;

  .target {
    margin: .5em;
    font-size: 1rem;
    padding: .5em;
    border: 1px solid #ccc;
    border-color: #dbdbdb #d2d2d2 #d0d0d0 #d2d2d3;
    box-shadow: inset 0.1em 0.1em 0.15em rgba(0,0,0,.1);
    vertical-align: middle;
    line-height: 1.25em;
    outline: 0;
    width: 10%;
    height: 80%;
  }
  input{
    margin: .5em;
    margin-right: 1rem;
    font-size: 1rem;
    padding: .5em;
    border: 1px solid #ccc;
    border-color: #dbdbdb #d2d2d2 #d0d0d0 #d2d2d3;
    box-shadow: inset 0.1em 0.1em 0.15em rgba(0,0,0,.1);
    vertical-align: middle;
    line-height: 1.25em;
    outline: 0;
    flex: 1;
    height: 80%;
  }
  .button{
    width: 10%;
    height: 70%;
  }
}